.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-wrapper-absolute {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		height: 80%;
		@include max-lg {
			transform: translate(-90%, -50%);
		}
		.background-element {
			height: 100%;
			text-align: center;
			img {
				height: 100%;
				width: auto;
				object-fit: fill;
			}
		}
	}
	.image-wrapper {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 80%;
		&::after {
			position: absolute;
			inset: 0;
			content: "";
			background-color: rgba($color: #000000, $alpha: 0.15);
		}
		.venue-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center center;
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
		}
		.venue-content {
			position: relative;
			text-align: center;
			.card {
				border: none;
				background-color: transparent;
				height: 100%;
				border-radius: 16px;
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					width: calc(100% - 13rem);
					height: 100%;
					border-radius: var(--border-radius);
					background-color: var(--color-primary);
					opacity: 0.6;
					content: "";
					@include max-lg {
						width: 100%;
						height: calc(100% - 13rem);
						top: unset;
						bottom: 0;
					}
					@include max-md {
						height: calc(100% - 10rem);
					}
				}
				.card-body {
					padding: 0 1rem;
					@include max-lg {
						padding: 0 0 2rem;
					}
					h4 {
						font-weight: 600;
						margin-bottom: 1rem;
						font-family: $font-secondary;
					}
					.ribbon-venue {
						position: absolute;
						top: 50%;
						left: 100%;
						width: fit-content;
						padding: 0.75rem 0.5rem;
						background-color: var(--color-primary);
						transform: translate(-50%, -50%);
						border-radius: 1000px;
						z-index: 5;
						@include max-lg {
							top: 100%;
							left: 50%;
						}
						svg {
							width: 40px;
							height: auto;
							margin-bottom: -0.5rem;
							path {
								stroke: var(--text-button);
							}
						}
					}
					.event-content {
						display: grid;
						grid-template-columns: auto 1fr;
						align-items: center;
						gap: 2.5rem;
						text-align: left;
						@include max-lg {
							grid-template-columns: 1fr;
							grid-template-rows: auto 1fr;
							text-align: center;
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 2rem;
		border-radius: 10px;
		text-align: center;
		z-index: 2;
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
		.title-section {
			&::before {
				all: unset;
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		gap: 3rem;
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-name {
		position: relative;
		padding: 5rem 7rem;
		background-color: var(--color-secondary);
		color: var(--text-button);
		aspect-ratio: 1;
		mask-image: url(../images/polygon-mask.png);
		mask-size: 100%;
		mask-repeat: no-repeat;
		mask-position: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		@include max-md {
			padding: 2rem 1rem;
		}
		h4 {
			width: 156px;
		}
		.date {
			position: relative;
			border-top: 1px solid var(--text-button);
			border-bottom: 1px solid var(--text-button);
			width: fit-content;
			margin: 0.5rem auto;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
		@include max-lg {
			padding: 0 1rem;
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
