.header-section {
	position: relative;
	width: 100%;
	height: auto;
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 30%;
		transform: translateY(-50%);
		@include max-lg {
			width: 40%;
			transform: translateY(-75%);
		}
		@include max-md {
			transform: translateY(-50%);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 30%;
		transform: translateY(-50%) scaleX(-1);
		@include max-lg {
			width: 40%;
			transform: translateY(-75%) scaleX(-1);
		}
		@include max-md {
			transform: translateY(-50%) scaleX(-1);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 38%;
		transform: translate(-25%, -50%);
		@include max-md {
			width: 35%;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 38%;
		transform: translate(25%, -50%) scaleX(-1);
		@include max-md {
			width: 35%;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.scroll-icon {
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translate(-50%, 0%);
		z-index: 5;
	}
	.image-wrapper {
		position: relative;
		width: 60%;
		margin: 0 auto;
		aspect-ratio: 1;
		padding: 0.75rem;
		@include max-lg {
			width: 100%;
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
			mask-image: url(../images/polygon-mask.png);
			mask-size: 100%;
			mask-repeat: no-repeat;
		}
	}
	.header-content {
		position: relative;
		text-align: left;
		padding: 4rem 0 0;
		.header {
			position: relative;
			display: grid;
			grid-template-rows: 1fr auto;
			align-items: center;
			gap: 1rem;
			.header-title {
				position: relative;
				text-align: center;
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
