.stream-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
		transform: translate(-30%, -40%);
		@include max-md {
			width: 160px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.stream-castle-background {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		height: 100%;
		width: auto;
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			width: 65%;
			height: 100%;
			background-color: var(--color-primary);
			opacity: 0.5;
			box-shadow: 0 0 1rem rgba($color: #fff, $alpha: 1);
			border-radius: var(--border-radius);
			content: "";
			@include max-lg {
				width: 100%;
			}
		}
		.stream-content {
			position: relative;
			padding: 3rem 2rem;
			text-align: left;
			@include max-lg {
				padding: 2rem 0;
				text-align: center;
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
