.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	&.without-image {
		.story-body {
			&::before {
				height: 100%;
			}
			.story-description {
				margin-top: 0;
				&::before {
					width: 100%;
				}
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 80%;
		transform: translate(30%, -30%);
		@include max-lg {
			width: 60%;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 2rem;
		left: 0;
		width: 40%;
		transform: translateX(-55%);
		@include max-lg {
			width: 30%;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.story-body {
		position: relative;
		&::before {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: calc(100% - 5rem);
			background-color: rgba($color: #fff, $alpha: 0.5);
			box-shadow: 0 0 1rem rgba(0, 0, 0, 0.15);
			border-radius: var(--border-radius);
			content: "";
		}
		.story-castle-background {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			height: calc(100% - 8rem);
			@include max-lg {
				width: 100%;
				height: auto;
				max-height: 100%;
				object-fit: cover;
			}
		}
	}
	.story-content {
		position: relative;
		border-radius: var(--border-radius);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		@include min-lg {
			.title-section {
				&::before {
					transform: translate(-50%, -125%);
				}
			}
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 2rem;
		@include min-lg {
			margin-top: 5rem;
		}
		@include max-lg {
			margin-top: 2rem;
		}
		&::before {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120%;
			height: 100%;
			background-color: var(--color-primary);
			box-shadow: 0 0 1rem rgba($color: #000, $alpha: 0.15);
			content: "";
			border-radius: 16px 16px 0 16px;
			@include max-lg {
				width: 100%;
				height: 120%;
				border-radius: 16px 16px 0 0;
			}
		}
		@include max-md {
			padding: 2rem 1rem;
		}
		* {
			position: relative;
		}
	}
	.image-story-wrap {
		position: relative;
		width: 100%;
		aspect-ratio: 1;
		border-radius: var(--border-radius);
		border: 3px solid #bababa;
		padding: 0.5rem;
		z-index: 3;
		.story-image {
			position: relative;
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: var(--border-radius);
		}
	}
}
