.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: -5rem;
		left: 0;
		width: 40%;
		@include max-md {
			top: 0;
			width: 45%;
			transform: translateX(-10%);
		}
		.rose {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 35%;
			transform-origin: 0% 100%;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: -5rem;
		right: 0;
		width: 40%;
		transform: scaleX(-1);
		@include max-md {
			top: 0;
			width: 45%;
			transform: translateX(10%) scaleX(-1);
		}
		.rose {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 35%;
			transform-origin: 0% 100%;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		aspect-ratio: 1;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-castle-background {
		height: 100%;
		width: auto !important;
		object-fit: fill !important;
	}
	.couple-content {
		position: relative;
		border-radius: var(--border-radius);
		padding: 2rem;
		background-color: var(--color-primary);
		@include max-md {
			padding: 2rem 1rem;
		}
		.title-section {
			&::before {
				transform: translate(-50%, -120%);
			}
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem;
		border-radius: 12px;
		overflow: hidden;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 1;
			z-index: 3;
			padding: 0.5rem;
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				mask-image: url(../images/polygon-mask.png);
				mask-size: 100%;
				mask-repeat: no-repeat;
				mask-position: center;
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 5% 6rem;
			&::before {
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
				width: 90%;
				height: calc(100% + 12rem);
				background-color: var(--color-primary);
				opacity: 0.6;
				mask-image: url(../images/union-mask.png);
				mask-size: 100%;
				mask-repeat: no-repeat;
				mask-position: bottom;
				content: "";
				@include max-md {
					width: 95%;
				}
			}
			* {
				position: relative;
			}
		}
		.couple-parent {
			width: 100%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-button-trans);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: linear-gradient(var(--color-light), var(--color-light)) padding-box, var(--color-gradient) border-box;
				border: 1px solid transparent;
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				&:hover {
					background: var(--color-secondary);
					color: var(--text-button);
				}
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
