.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: -3rem;
		left: 0;
		width: 40%;
		@include max-md {
			top: 0;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: -3rem;
		right: 0;
		width: 40%;
		transform: scaleX(-1);
		@include max-md {
			top: 0;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 38%;
		transform: translate(-25%, -50%);
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 38%;
		transform: translate(25%, -50%) scaleX(-1);
		img {
			transform-origin: 100% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 1;
		padding: 0.5rem;
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			mask-image: url(../images/polygon-mask.png);
			mask-size: 100%;
			mask-position: center;
			mask-repeat: no-repeat;
		}
	}
	.thank-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		.title-section {
			&::before {
				all: unset;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
