.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: var(--color-primary);
			border-radius: var(--border-radius);
			opacity: 0.5;
			content: "";
		}
		.title-section {
			&::before {
				transform: translate(-50%, -155%);
				@include max-md {
					transform: translate(-50%, -120%);
				}
			}
		}
	}
}
