.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translateY(-50%);
		@include max-md {
			width: 180px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 20%;
		right: 0;
		width: 140px;
		@include max-md {
			width: 100px;
			top: -4rem;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		height: 100%;
		width: auto;
		margin: 0 auto;
		border-radius: var(--border-radius);
		aspect-ratio: 1;
		padding: 0.5rem;
		border: 3px solid #bababa;
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: var(--border-radius);
		}
	}
	.rsvp-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		.title-section {
			&::before {
				all: unset;
			}
		}
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-md {
				padding: 2rem 1rem 1rem;
			}
		}
	}
}
