.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 30%;
		transform: translateY(50%);
		@include max-lg {
			width: 40%;
		}
		@include max-md {
			transform: translateY(30%);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 30%;
		transform: translateY(50%) scaleX(-1);
		@include max-lg {
			width: 40%;
		}
		@include max-md {
			transform: translateY(30%) scaleX(-1);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 50%;
		img {
			transform-origin: 80% 100%;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0;
			align-items: center;
		}
		@include max-md {
			padding: 4rem 0 8rem;
		}
		.cover-content {
			text-align: center;
			position: relative;
			border-radius: 12px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			gap: 3rem;
			height: 100%;
			@include max-lg {
				gap: 2rem;
			}
			@include max-md {
				gap: 1rem;
			}
			.cover-body {
				position: relative;
				display: grid;
				grid-template-columns: repeat(3, 1fr);
				align-items: center;
				justify-content: center;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				@include max-lg {
					grid-template-columns: 1fr;
					grid-template-rows: auto auto auto;
					width: 60%;
					height: 80%;
				}
				@include max-md {
					width: 90%;
					height: 100%;
				}
				@include max-xs {
					height: 100%;
					width: 90%;
				}
				.castle-background {
					position: absolute;
					top: 0;
					left: 0;
					height: 100%;
					width: auto;
					@include max-lg {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: var(--border-radius);
					}
				}
				.greeting-wrapper {
					position: relative;
					padding: 0.5rem 0;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: 120%;
					aspect-ratio: 1;
					margin-top: -10%;
					@include max-lg {
						margin-top: 0;
						height: 100%;
					}
					@include max-md {
						width: 105%;
						margin-left: -2.5%;
					}
					@include max-xs {
						width: 70%;
						margin: 0 auto;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						mask-image: url(../images/polygon-mask.png);
						mask-repeat: no-repeat;
						mask-position: center;
						mask-size: 100%;
					}
				}
				.cover-description {
					padding: 1rem;
					text-align: center;
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
